const ADD_NEW_ARTICLE_MAIN = 'ADD_NEW_ARTICLE_MAIN';
const DELETE_ARTICLE_MAIN = 'DELETE_ARTICLE_MAIN';
const CHANGE_ARTICLE_MAIN = 'CHANGE_ARTICLE_MAIN';
const SEND_POSTER_MAIN = 'SEND_POSTER_MAIN';
const SEND_POSTER_DESSERT = 'SEND_POSTER_DESSERT';


let initialState = {
    mainData: {
        articleData: [
            {id: 1,fio: "fio",organization: "organization",position: "position",academicDegree: "academicDegree",
                country: "country",сity: "сity",address: "address",email: "Email",nameReport:"report",nameFile: "name.docx"},
            {id: 2,fio: "fio2",organization: "organization2",position: "position2",academicDegree: "academicDegree2",
                country: "country2",сity: "сity2",address: "address2",email: "Email2",nameReport:"report2",nameFile: "name2.docx"}
        ]
    },

    count: 0
}

const AddReducer = (state = initialState, action) =>  {
    switch (action.type) {
        case ADD_NEW_ARTICLE_MAIN:
            let dataArticle = state.mainData.articleData.push(action.obj)
            return {
                ...state,
                mainData: { ...state.mainData, dataArticle }
            }
        case CHANGE_ARTICLE_MAIN:
            let id = action.id
            let objectA = action.obj
            let articleData = state.mainData.articleData.map((e)=>{
                if(id == e.id){
                    return objectA
                }else {
                    return e
                }
            })
            return {
                ...state,
                mainData: { ...state.mainData, articleData }
            }
        case DELETE_ARTICLE_MAIN:
            let id2 = action.id
            let articleData2 = state.mainData.articleData.filter((e)=>{

                if(e){
                    if(id2 != e.id){
                        return e
                    }
                }

            })
            return {
                ...state,
                mainData: { ...state.mainData, articleData: articleData2 }
            }

        default:
            return state;
    };
};


export let addNewArticle = (obj) =>{
    return {
        type: ADD_NEW_ARTICLE_MAIN,
        obj
    }
}

export let changeNewArticle = (obj,id) =>{
    return {
        type: CHANGE_ARTICLE_MAIN,
        obj,id
    }
}
export let deleteArticle = (id) =>{
    return {
        type: DELETE_ARTICLE_MAIN,
        id
    }
}

