import React, {useState} from 'react';
import style from '../ChageArticle.module.css';







function ChageArticleElem (props) {
        const [show,setShow] = useState(true)

    const BlurFunction = () => {
        setShow(true)
    }
    const  DoubleClickFunction = () => {
        setShow(false)
    }
    const editText = (e) =>{
        let text = e.target.value
        props.edit(text)
    }
        return (
            <div className={style.element}>
                <span>{props.text}:</span>
                {show ?
                    <p onClick={DoubleClickFunction}>{props.data}</p>
                    :
                    <input onChange={editText} autoFocus={true} onBlur={BlurFunction} value={props.data}   name={"fio"}  className={style.formStyleInputText} type="text"/>
                }
            </div>
        );
    }


export default ChageArticleElem;