import React, {useState} from 'react';
import style from './Main.module.css';
import {NavLink, Redirect, Route} from "react-router-dom";
import HeaderContainer from "../Header/HeaderCon";
import AddArticleContainer from "../AddArticle/AddArticleCon";
import PanelContainer from "../Panel/PanelCon";

import WithRouterChangeArticleContainer from "../ChangeArticle/ChageArticleCon";




function Main (props) {

        return (
            <div className={style.Main}>
                <div className={style.Container}>
                    <div className={style.child}>
                        <div className={style.left}>
                            <Route exact path="/" render={() => <Redirect to={"/add"} />   }/>
                            <Route exact path="/add" render={() => <AddArticleContainer/>}/>
                            <Route exact path="/panel" render={() => <PanelContainer/>}/>
                            <Route exact path="/article/:id?" render={() => <WithRouterChangeArticleContainer/>}/>
                        </div>
                        <div className={style.menu}>
                            <NavLink activeStyle={{
                                color: "grey",
                                borderLeftColor: "grey",
                                cursor: "default"
                            }} to={'/add'} className={style.elem_menu}><h3>Добавить статью</h3></NavLink>
                            <NavLink activeStyle={{
                                color: "grey",
                                borderLeftColor: "grey",
                                cursor: "default"
                            }} to={'/panel'} className={style.elem_menu}><h3>Панель</h3></NavLink>
                        </div>
                    </div>
                </div>
            </div>
        );

}

export default Main;