import React, {useState} from 'react';
import style from './Panel.module.css';
import {NavLink, Redirect, Route} from "react-router-dom";
import HeaderContainer from "../Header/HeaderCon";
import AddArticleContainer from "../AddArticle/AddArticleCon";




function Panel (props) {

    let newObject = []
    const  deleteElem = (id) => {
        props.deleteArticle(id)
    }
    if(props.articleData){
        let propsObject = props.articleData
        newObject = propsObject.map( (e)=>{
            if(e){
                return (
                    <div className={style.elem}>
                        <div className={style.elem_container}>
                            <div className={style.name}>
                                {e && e.nameReport}
                            </div>
                            <div className={style.fio}>
                                {e && e.fio}
                            </div>
                            <div className={style.button_group}>
                                <NavLink to={"/article/" + (e && e.id)} className={style.edit + " " + style.button}>Редактировать</NavLink>
                                <p onClick={() => deleteElem(e && e.id)} className={style.delete + " " + style.button}>Удалить</p>
                            </div>
                        </div>
                    </div>
                )
            }

        })
    }

        return (
            <div className={style.Main}>
                {newObject}
            </div>
        );

}

export default Panel;