import React from 'react';
import {compose} from "redux";
import {connect} from "react-redux";
import ChageArticle from "./ChageArticle";
import {withRouter} from "react-router-dom";
import {changeNewArticle} from "../../redux/AddReduser";



function ChangeArticleContainer (props) {



    let newObj
    if(props.articleData){
        newObj = props.articleData.filter((e) => {
            if(e){
                if( e.id == props.match.params.id){
                    return e
                }
            }
        })
    }
    return (
        <ChageArticle changeNewArticle={props.changeNewArticle} data={newObj}  />
    )
}


let mapStateToProps = (state) => {
    return {
        isAuth: state.isAuthM.isAuth,
        articleData: state.Add.mainData.articleData
    }
}
let WithRouterChangeArticleContainer = withRouter(ChangeArticleContainer)

export default compose(
    connect(mapStateToProps , {
        changeNewArticle
    })
)(WithRouterChangeArticleContainer)