import React, {useState} from 'react';
import style from './AddArticle.module.css';
import {Field, reduxForm} from "redux-form";
import arrow from "../../file/image/downArrow.svg"







function AddArticleForm (props) {

    const [stileInput,setStileInput] = useState(false)
    const [fileAdd,setFileAdd] = useState("")
    const [checkExpansion,setCheckExpansion] = useState(false)
    const treatmentFileInput = (e) =>{
        let text
        let fileAdd
        if (e.target.files[0]){
            text = e.target.files[0].name
            fileAdd = e.target.files[0]
            props.setFileNameFun(text)
            let expansion = ''
            let check = true
            for(let i = text.length-1; i > 0;i--){
                if(text[i] != '.'){
                    if(check) expansion = expansion + text[i]
                }else check = false
            }
            expansion = expansion.split("").reverse().join("");
            if(expansion == "docx"){
                setFileAdd(text)
                props.setFileItself(fileAdd)
                setCheckExpansion(true)
            }else {
                setFileAdd("Файл не верного расширения")
                setCheckExpansion(false)
            }
        }

    }
    const ChangeStyle = () =>{
        setStileInput(!stileInput)
    }




    return (
        <div className={style.formStyleMain} >
            <h3>Добавление</h3>
            <form className={style.formStyle}  onSubmit={props.handleSubmit}>
                <span>ФИО<b>*</b></span>
                <Field  component={"input"} name={"fio"}  className={style.formStyleInputText} type="text"/>
                <span>Организация</span>
                <Field  component={"input"} name={"organization"}  className={style.formStyleInputText} type="text"/>
                <span>Должность</span>
                <Field  component={"input"} name={"position"}  className={style.formStyleInputText} type="text"/>
                <span>Ученая степень</span>
                <Field  component={"input"} name={"academicDegree"}  className={style.formStyleInputText} type="text"/>
                <span>Страна</span>
                <Field  component={"input"} name={"country"}  className={style.formStyleInputText} type="text"/>
                <span>Город</span>
                <Field  component={"input"} name={"сity"}  className={style.formStyleInputText} type="text"/>
                <span>Рабочий адрес</span>
                <Field  component={"input"} name={"address"}  className={style.formStyleInputText} type="text"/>
                <span>E-mail </span>
                <Field  component={"input"} name={"Email"}  className={style.formStyleInputText} type="text"/>
                <span>Название доклада </span>
                <Field  component={"textarea"} name={"nameReport"}  className={style.formStyleInputTextarea} type="text"/>
                <div  className={style.example + " " + (stileInput && style.borderStyle)}>
                    <div className={style.formgroup}>
                        <label className={style.label}>
                            {
                                fileAdd.length>0 ?
                                    <p className={style.materialicons}>{fileAdd}</p>
                                    :
                                    <div>
                                        <span className={style.title}>Добавить файл</span>
                                        <div className={style.arrow_group}>
                                            <img  className={style.arrow + " " + (stileInput && style.arrowAnime) } src={arrow} alt="arrow"/>
                                            <span></span>
                                        </div>
                                    </div>
                            }
                            <input onMouseLeave={ChangeStyle}  onMouseOver={ChangeStyle} type="file" name={"dox"} onChange={treatmentFileInput} />
                        </label>
                    </div>
                </div>
                <button className={"ButtonMain" + " " + style.marginT}>Отправить</button>
            </form>
        </div>
    );
}

const AddArticleReduxForm = reduxForm({
    form: 'addArticle'
})(AddArticleForm)



function AddArticle (props) {

    const [addCheck,setAddCheck] = useState(false)
    const [redirectCheck,setRedirectCheck] = useState(false)
    const [fileName,setFileName] = useState("")
    const [fileItself,setFileItself] = useState('')



    const onSubmitForm = (e) =>{
        let fio = e.fio;
        let organization = e.organization;
        let position = e.position;
        let academicDegree = e.academicDegree;
        let country = e.country;
        let сity = e.сity;
        let address = e.address;
        let Email = e.Email;
        let nameReport = e.nameReport;
        let nameFile = fileName;
        console.log(fileName)
        let obj = {
            id: Math.floor(Math.random() * 100000000),
            fio: fio,
            organization: organization,
            position: position,
            academicDegree: academicDegree,
            country: country,
            сity: сity,
            address: address,
            email: Email,
            nameReport: nameReport,
            nameFile: nameFile,
            file: fileItself
        }

        if(fio && organization && position && academicDegree && country && сity && fileName){
            props.addNewArticle(obj)
            setRedirectCheck(true)
        }else {
            setAddCheck(true)
        }

    }

    const CloseError = () => {
        setAddCheck(false)
    }

    const AddMore = () => {
        setRedirectCheck(false)
    }
        return (
            <div className={style.main}>
                {addCheck &&
                <span className={style.notification}>Заполните все нужные поля!
                     <div onClick={CloseError} className={style.closeButton}>x</div>
                </span>
                }
                {redirectCheck ?
                    <div className={style.titleSuccessfulCon}>
                        <div className={style.titleSuccessfulAdd}>Добавлено!</div>
                        <span onClick={AddMore} className={style.titleSuccessfulButton}>Добавить ещё</span>
                    </div>

                    :
                    <AddArticleReduxForm setFileItself={setFileItself} setFileNameFun={setFileName}  onSubmit={onSubmitForm} />
                }

            </div>
        );
    }


export default AddArticle;