import React, {useEffect, useState} from 'react';
import style from './ChageArticle.module.css';
import {Field, reduxForm} from "redux-form";
import arrow from "../../file/image/downArrow.svg"
import ChageArticleElem from "./ChangeArticleElement/ChageArticleElem";
import {Redirect} from "react-router-dom";






function ChageArticle (props) {

    const [valueName,setValueName] = useState("")
    const [valueFIO,setValueFIO] = useState("")
    const [valueOrganization,setValueOrganization] = useState("")
    const [valuePosition,setValuePosition] = useState("")
    const [valueAcademicDegree,setValueAcademicDegree] = useState("")
    const [valueCountry,setValueCountry] = useState("")
    const [valueCity,setValueCity] = useState("")
    const [valueAddress,setValueAddress] = useState("")
    const [valueEmail,setValueEmail] = useState("")
    const [valueNameFile,setValueNameFile] = useState("")
    const [idArticle,setIdArticle] = useState(0)
    const [redirectBool,setRedirectBool] = useState(false)
    const [fileChange,setFileChange] = useState('')

    const [stileInput,setStileInput] = useState(false)
    const [fileAdd,setFileAdd] = useState("")
    const [checkExpansion,setCheckExpansion] = useState(false)

    if(props.data[0]){
        //setValueName(props.data[0].nameReport)
    }
    useEffect(()=>{
        setValueName(props.data[0].nameReport)
        setValueFIO(props.data[0].fio)
        setValueOrganization(props.data[0].organization)
        setValuePosition(props.data[0].position)
        setValueAcademicDegree(props.data[0].academicDegree)
        setValueCountry(props.data[0].country)
        setValueCity(props.data[0].сity)
        setValueAddress(props.data[0].address)
        setValueEmail(props.data[0].email)
        setValueNameFile(props.data[0].nameFile)
        setIdArticle(props.data[0].id)
        setFileChange(props.data[0].file)
    },[])



    const onSubmitForm = () =>{
        let obj = {
            id: idArticle,
            fio: valueFIO,
            organization: valueOrganization,
            position: valuePosition,
            academicDegree: valueAcademicDegree,
            country: valueCountry,
            сity: valueCity,
            address: valueAddress,
            email: valueEmail,
            nameReport: valueName,
            file: fileChange,
            nameFile: valueNameFile
        }
        props.changeNewArticle(obj,idArticle)
        setRedirectBool(true)
    }
    const closeForm = () =>{
        setRedirectBool(true)
    }

    const ChangeStyle = () =>{
        setStileInput(!stileInput)
    }

    const treatmentFileInput = (e) =>{
        let text
        let fileAdd
        if (e.target.files[0]){
            text = e.target.files[0].name
            fileAdd = e.target.files[0]

            let expansion = ''
            let check = true
            for(let i = text.length-1; i > 0;i--){
                if(text[i] != '.'){
                    if(check) expansion = expansion + text[i]
                }else check = false
            }
            expansion = expansion.split("").reverse().join("");
            if(expansion == "docx"){
                setFileAdd(text)
                setValueNameFile(text)
                setFileChange(fileAdd)
                setCheckExpansion(true)
            }else {
                setFileAdd("Файл не верного расширения")
                setCheckExpansion(false)
            }
        }
    }



        if(redirectBool){
            return <Redirect  to="/panel" />
        }else {
            return (
                <div className={style.main}>
                    <p>*Кликните для редактирования</p>
                    <ChageArticleElem edit={setValueFIO} data={valueFIO} text={"ФИО"}/>
                    <ChageArticleElem edit={setValueOrganization} data={valueOrganization} text={"Организация"}/>
                    <ChageArticleElem edit={setValuePosition} data={valuePosition} text={"Должность"}/>
                    <ChageArticleElem edit={setValueAcademicDegree} data={valueAcademicDegree} text={"Ученая степень"}/>
                    <ChageArticleElem edit={setValueCountry} data={valueCountry} text={"Страна"}/>
                    <ChageArticleElem edit={setValueCity} data={valueCity} text={"Город"}/>
                    <ChageArticleElem edit={setValueAddress} data={valueAddress} text={"Рабочий адрес"}/>
                    <ChageArticleElem edit={setValueEmail} data={valueEmail} text={"E-mail"}/>
                    <ChageArticleElem edit={setValueName} data={valueName} text={"Название доклада"}/>

                    <div className={style.document}>
                        <span>Изменить файл ({valueNameFile})</span>
                        <div  className={style.example + " " + (stileInput && style.borderStyle)}>
                            <div className={style.formgroup}>
                                <label className={style.label}>
                                    {
                                        fileAdd.length>0 ?
                                            <p className={style.materialicons}>{fileAdd}</p>
                                            :
                                            <div>
                                                <span className={style.title}>Добавить файл</span>
                                                <div className={style.arrow_group}>
                                                    <img  className={style.arrow + " " + (stileInput && style.arrowAnime) } src={arrow} alt="arrow"/>
                                                    <span></span>
                                                </div>
                                            </div>
                                    }
                                    <input onMouseLeave={ChangeStyle}  onMouseOver={ChangeStyle} type="file" name={"dox"} onChange={treatmentFileInput} />
                                </label>
                            </div>
                        </div>
                    </div>
                    <div className={style.buttonGroup}>
                        <div onClick={onSubmitForm} className={style.buttonSave}>Сохранить</div>
                        <div onClick={closeForm} className={style.buttonCansler}>Отмена</div>
                    </div>
                </div>
            );
        }

    }


export default ChageArticle;